import React from "react";
import { Helmet } from "react-helmet";
import { Checkbox, Toast } from "antd-mobile";
import VerifyPage from "./VerifyPage";
import Agreement from "components/common/Agreement";
import banner from "assets/verify-banner.png";

let timer = null;

export default class extends React.PureComponent {
  state = {
    phone: "",
    code: "",
    sent: false,
    seconds: 60,
    checked: true,
    showAgreement: false
  };
  handleInput = ({ target }) => {
    this.setState({
      [target.name]: target.value
    });
  };
  handleCheck = ({ target }) => {
    this.setState({
      checked: target.checked
    });
  };
  sendSMS = () => {
    if (!this.state.sent) {
      const { phone } = this.state;
      if (phone.length !== 11 || isNaN(phone)) {
        Toast.info("请输入正确的手机号码！", 1.6);
        return;
      }
      Toast.hide();
      setTimeout(() => {
        this.countdown();
        Toast.loading("发送中...", 0);
      }, 1);
      setTimeout(() => {
        Toast.info('发送成功！', 1.6);
      }, 500);
    }
  };
  countdown = interrupt => {
    if (!!interrupt) {
      clearTimeout(timer);
    } else {
      this.setState({ sent: true });
      this.count(this.state.seconds);
    }
  };
  count = seconds => {
    if (seconds > 0) {
      seconds -= 1;
      this.setState({ seconds });
      timer = setTimeout(() => {
        this.count(seconds);
      }, 1000);
    } else {
      clearTimeout(timer);
      this.setState({ sent: false, seconds: 60 });
    }
  };
  submit = () => {
    if (this.state.checked) {
      if (this.state.phone.length !== 11 || isNaN(this.state.phone)) {
        Toast.info("请输入正确的手机号码！", 1.6);
        return;
      }
      if (this.state.code.length !== 6 || isNaN(this.state.code)) {
        Toast.info("请输入正确的验证码！", 1.6);
        return;
      }
      this.props.history.push(`/progress${this.props.location.search}`);
    }
  };
  render() {
    return (
      <>
        <VerifyPage>
          <Helmet>
            <title>大数据信用查询</title>
          </Helmet>
          <VerifyPage.Banner
            src={banner}
            alt="banner"
            onClick={() => false}
          />
          <VerifyPage.Content>
            <VerifyPage.Input>
              <input
                type="tel"
                name="phone"
                maxLength="11"
                placeholder="请输入您的手机号"
                onInput={this.handleInput}
              />
            </VerifyPage.Input>
            <VerifyPage.Input>
              <input
                type="tel"
                name="code"
                maxLength="6"
                placeholder="请输入6位数字验证码"
                onInput={this.handleInput}
              />
              <button
                className={this.state.sent ? "D" : ""}
                onClick={this.sendSMS}
              >
                {this.state.sent ? `${this.state.seconds}s` : "获取验证码"}
              </button>
            </VerifyPage.Input>
            <VerifyPage.Agreement>
              <Checkbox
                onChange={this.handleCheck}
                defaultChecked={this.state.checked}
              />
              我已阅读并同意
              <span
                onClick={() => {
                  this.setState({ showAgreement: true });
                }}
              >
                《大数据查询授权协议》
              </span>
            </VerifyPage.Agreement>
            <VerifyPage.SubmitButton
              className={!this.state.checked ? "D" : ""}
              onClick={this.submit}
            >
              下一步
            </VerifyPage.SubmitButton>
          </VerifyPage.Content>
        </VerifyPage>
        <Agreement
          show={this.state.showAgreement}
          close={() => {
            this.setState({ showAgreement: false });
          }}
        />
      </>
    );
  }
}
