import React from "react";
import Styled from "styled-components";
import close from 'assets/close.png';

const Popup = Styled.div`
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0,0,0,0.4);
    position: fixed;
    z-index: 99;
`;

Popup.Dialog = Styled.div`
    width: 8.2667rem;
    height: 9.2rem;
    top: 45%;
    left: 50%;
    margin-top: -4.6rem;
    margin-left: -4.1334rem;
    display: flex;
    flex-direction: column;
    background-color: #FFFFFF;
    border-radius: 8px;
    position: absolute;
`;

Popup.Close = Styled.img`
    width: 0.4rem;
    height: 0.4rem;
    top: 0.2134rem;
    right: 0.2134rem;
    display: block;
    position: absolute;
    z-index: 999;
`;

Popup.CloseButton = props => (
  <Popup.Close
    {...props}
    src={close}
  />
);

Popup.Content = Styled.div`
    flex-grow: 1;
    padding: 0.5334rem;
    color: #4A4A4A;
    font-size: 0.4267rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow-y: auto;
    position: relative;
`;

Popup.Content.Title = Styled.div`
    color: #4EA0FF;
    font-size: 0.4rem;
    line-height: 0.5334rem;
    text-align: center;
`;

Popup.Content.Tips = Styled.div`
    color: #B8B5B5;
    font-size: 0.32rem;
    line-height: 0.4267rem;
    text-align: center;
`;

Popup.Content.Highlight = Styled.em`
    color: #4EA0FF;
    font-style: normal;
`;

Popup.Buttons = Styled.div`
    width: 100%;
    height: 1.12rem;
    display: flex;
    justify-content: space-between;
    position: relative;

    button {
        flex-basis: 50%;
        height: 100%;
        margin: 0;
        padding: 0;
        font-size: 0.3734rem;
        line-height: 1.12rem;
        border: none;

        &:first-of-type {
            color: #4EA0FF;
            background-color: #FFFFFF;
            border-top: 1px solid #EEEEEE;
            border-bottom-left-radius: 8px;
        }

        &:last-of-type {
            color: #FFFFFF;
            background-color: #4EA0FF;
            border-bottom-right-radius: 8px;
        }
    }
`;

export default Popup;
