import Styled from 'styled-components';

const Button = Styled.button`
    width: 100%;
    padding: 0.32rem 0;
    color: #FFFFFF;
    font-size: 0.4rem;
    background-color: #4EA0FF;
    border: none;
    border-radius: 3px;

    &.D {
        background-color: #D8D8D8;
    }
`;

export default Button;