import React from "react";
import Styled from "styled-components";
import Popup from "components/common/Popup";

const Agreement = Styled(Popup.Dialog)`
    width: 8rem;
    height: 10.1334rem;
    margin-top: -5.0667rem;
    margin-left: -4rem;
`;
Agreement.Content = Styled(Popup.Content)`
    font-size: 0.3467rem;
    line-height: 0.56rem;
    overflow-y: auto;

    p {
        margin-bottom: 0.08rem;

        &:before {
            content: '　 ';
        }
    }
`;
Agreement.Title = Styled.p`
    margin-bottom: 0.5334rem!important;
    font-size: 0.4267rem;
    text-align: center;

    &:before {
        content: ''!important;
    }
`;

export default class extends React.PureComponent {
  render() {
    return (
      <Popup style={{ display: this.props.show ? "block" : "none" }}>
        <Agreement>
          <Popup.CloseButton onClick={this.props.close} />
          <Agreement.Content>
            <Agreement.Title>大数据查询授权协议</Agreement.Title>
            <p>
              <b>重要提示：</b>
            </p>
            <p>
              <b>
                尊敬的客户：为了保障您的合法权益，您应当阅读并遵守本授权书。请您务必审慎阅读，并充分理解本授权书的全部内容，特别是以加粗形式提示您注意的。{" "}
              </b>
            </p>
            <p>
              <b>
                若您不接受本授权书的任何条款，请您立即停止授权。若您未停止授权，或进行了支付操作等同于同意授权，均视为您已阅读并接受本授权书的约束。
              </b>
            </p>
            <p>
              <b>
                本人清楚理解以下授权的含义并同意授权大数据信用查询进行以下事项：
              </b>
            </p>
            <p>
              （一）同意授权大数据信用查询通过合法存有本人信息的机构收集本人的信息，可进行相关处理（包括但不限于整理、保存、加工、提供）并向本人出具报告。收集信息的范围包括但不限于身份信息、交易信息、履约信息、联系信息、职业信息、政务信息、设备信息、资产信息。
              <b>
                本大数据查询未通过中国人民银行征信中心进行查询，不会对您的人民银行征信报告有任何影响。
              </b>
            </p>
            <p>
              （二）同意授权合法存有本人信息的机构向大数据信用查询提供本人的上述信息。
            </p>
            <p>
              <b>特别声明：</b>
            </p>
            <p>
              <b>
                本授权书经接受后即时生效，且效力具有独立性，不因相关业务合同或条款无效或被撤销而无效或失效，本授权一经作出，便不可撤销。
              </b>
            </p>
            <p>
              <b>
                本授权书签订地为中华人民共和国广东省深圳市龙岗区，本授权书的成立、生效、履行、解释及争议解决，适用中华人民共和国法律法规（不包括港澳台地区的法律法规及冲突法）。若您和被授权人之间发生任何争议，应友好协商解决；协商不成的，您与被授权人均同意以本授权书签订地人民法院为管辖法院。
              </b>
            </p>
            <p>
              <b>
                本人已知悉本授权书全部内容（特别是加粗字体内容）的含义及因此产生的法律效力，自愿作出以上授权。本授权书是本人真实意思表示，本人同意承担由此带来的一切法律后果。
              </b>
            </p>
            <p>
              <b>（以下无正文）</b>
            </p>
          </Agreement.Content>
        </Agreement>
      </Popup>
    );
  }
}
