import Styled from "styled-components";
import Button from "components/common/Button";

const VerifyPage = Styled.div`
    width: 100%;
    height: 100%;
    position: relative;
`;

VerifyPage.Banner = Styled.img`
    width: 100%;
    display: block;
`;

VerifyPage.Content = Styled.div`
    padding: 0.2667rem 0.5334rem;
    display: flex;
    flex-direction: column;
`;

VerifyPage.Input = Styled.div`
    width: 100%;
    height: 1.1174rem;
    margin-top: 0.4rem;
    display: flex;

    input {
        flex-grow: 1;
        height: 100%;
        padding: 0.2134rem 0.2667rem;
        margin: 0;
        color: #333333;
        font-size: 0.3734rem;
        border: 1px solid #E2E3E6;
        border-radius: 3px;

        &::-webkit-input-placeholder { 
            color: #C6C6C6; 
        } 
        &:-moz-placeholder { 
            color: #C6C6C6; 
        } 
        &::-moz-placeholder { 
            color: #C6C6C6; 
        } 
        &:-ms-input-placeholder { 
            color: #C6C6C6; 
        } 
    }

    button {
        flex-basis: 3.8667rem;
        height: 100%;
        margin: 0;
        margin-left: 0.2667rem;
        color: #FFFFFF;
        font-size: 0.3734rem;
        background-color: #4EA0FF;
        border: none;
        border-radius: 3px;

        &.D {
            background-color: #D8D8D8;
        }
    }
`;

VerifyPage.Agreement = Styled.div`
    width: 100%;
    margin-top: 0.4rem;
    color: #C6C6C6;
    font-size: 0.32rem;
    display: flex;
    align-items: center;

    span {
        color: #4EA0FF;
    }
`;

VerifyPage.SubmitButton = Styled(Button)`
    margin-top: 0.4rem;
`;

export default VerifyPage;